body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  font-family: "Poppins",sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.box{
width:100%;
height:100vh;
position:relative;
}
	video {
	  z-index:-2;
    width:100%; 
    object-fit: cover;
    height:100vh;
    filter:opacity(85%);
  }
.box-button{
background-color: #2872fa;
height:50px  ;
padding:12px 16px;
box-sizing: border-box;
display: flex;
align-items: center;
justify-content: center;
color:white;
position:absolute;
left:0;
right:0;
text-align: center;
margin:auto;
width: 230px;
border-radius: 5px;
bottom:110px
}
.box-name{
height:80px;
font-size:40px;
padding:12px 16px;
box-sizing: border-box;
display: flex;
align-items: center;
justify-content: center;
color:white;
z-index: 5;
font-weight: bolder;
position:absolute;
left:0;
right:0;
text-align: center;
margin:auto;
width: 100%;
border-radius: 5px;
bottom:190px
}
.searchbar {
  width:100%;
  top:1px;
  position:fixed;
  z-index: 1;
  color:black;
  padding:0px 20px 0px 20px;
  box-sizing: border-box;
  height:80px;
  background-color: white;
  display:flex;
  justify-content: center;
  align-items: center;
}
.searchbar div{
  width:50%;
  background-color: rgba(245, 237, 237, 0.644);
  height:70%;
  display:flex;

}
.searchbar input{
  border:none;
  width:100%;
  background-color:  rgba(245, 237, 237, 0.644);
  font-size: x-large;
  padding:5px 15px 5px 15px;
  box-sizing: border-box;
}
.searchbar input:focus{
  outline:none;
}
.searchbar > i{
  position:fixed;
  top:23px;
  bottom:23px;
  right:20px;
  font-size: 30px;
  z-index: 1;
  cursor: pointer;
}
.sidebar-container{
  position:fixed;
  top:0;
  width:350px;
  height:100vh;
  background-color: white;
  z-index:5;
}
.sidebar > span > i{
  display:none
}
.sidebar > span{
  font-size: x-large;
  margin:10px;
  cursor: pointer;
 width: 100%;
 padding:10px 25px 10px 25px;
 box-sizing: border-box;
 display:flex;
 justify-content: space-between;
}
.sidebar  > span *:not(.sidebar > span i):hover{
text-decoration: underline;
}
#sidebar-items:hover + #icon {
display:block
}
.sidebar::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
}
.sidebar-crossicon{
  position: absolute;
  top:25px;
  left:21px;
  display:flex;
  justify-content: space-around;
  align-items: center;
  width:70px;
  cursor: pointer;
}
.sidebar-crossicon span{
  font-size: large;
  font-stretch:ultra-expanded;
}
.sidebar{
  width:100%;
  height:100%;
  overflow: scroll;
  box-sizing: border-box;
  padding:10px;
  display:flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  margin-top: 65px;
  z-index:5
}
.navbar{
  width:100%;
  position:absolute;
  top:1px;
  z-index: 1;
  padding:0px 20px 0px 20px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  height:90px;
}
.navbar div{
display:flex;
height:100%;
flex-basis:270px;
align-items: center;
font-size:x-large;
cursor: pointer;
}
.navbar-theme1{
color:white
}
.navbar-theme2{
  color: black;
}
.sidebar-theme1{
  background-color: rgb(246, 246, 246);
}
.navbar-theme1:hover{
 background-color: white;
 color:black
}

 .div2{
  justify-content:center  ;
  text-align: center ;
  font-weight: bolder;
  font-size: xx-large !important;
}
.div3{
  justify-content: space-evenly;
}
.div3 > span:nth-child(2) > i {
  position:relative;
}
.div3 > span:nth-child(2) > i > span{
    position: absolute;
    background-color: red;
    color: white;
    text-align: center;
    z-index: -1;
   left:5px;
   min-width:14px;
    padding:3px;
    top: -18px;
    border-radius: 18px;
    font-size:17px ;
    z-index: -1;
}
.div1{
  justify-content: space-evenly;
}
.div3 span:nth-child(3){
  display:none
}
.div1 > i{
  display:none
}
@media only screen and (max-width: 800px) {
  .div3 span:nth-child(3){
    display:block;
    justify-content: center;
    
  }
  .div3 span:not(.div3 span:nth-child(3)){
    display:none
  }
  .div1 span{
   display:none
  }
  .div1 > i{
    display: block
  }
 .div3 {
  justify-content: flex-end;
 }
 .div1{
  justify-content: flex-start;
 }
 .searchbar div{
  width:80%
 }
 .sidebar{
  align-items: center;
  min-height: 20vh;
  max-height: 40vh;
  width:100%;
 }
 .sidebar-container{
   width: 100%;
   height:auto;  /*we have to override original height of 100vh  coming from original css to default so that we with default height of auto it totally depends on childs min and max height*/
  padding:10px;
  box-sizing: border-box;
 }
 .sidebar-crossicon{
  left:0 !important;
  right:0 !important;
  margin:auto !important
 }
 .sidebar > span{
  justify-content: center !important;
  width:70%;
  text-align: center;
  background-color: rgba(242, 240, 240, 0.701);
 }
  .sidebar > span:hover{
  background-color: lightgray;
 }

#sidebar-items:hover + #icon {
display:none;
}
.sidebar > span *:not(.sidebar > span i):hover{
text-decoration: none;
}
.products-navbar > span{
width:100%;
text-align: center;
}
.products-navbar{
  flex-wrap: wrap;
}
}

.products{
  margin-top:90px;
}
.products-navbar{
  height:70px;
  display:flex;
  align-items: center;
  justify-content:space-around;
  box-sizing: border-box;
}
hr{
  margin:0;
  padding:0
}
.products-container{
  display:flex;
   flex-wrap: wrap;
   box-sizing: border-box;
   justify-content: center;
   margin-top:20px;
   width:100%;
}
.product{
 display:flex;
 width:320px;
 flex-direction: column;
 align-items:center;
 margin:10px;
 cursor:pointer;
 font-weight: 570;

}
.product-title{
  margin-top:20px;
  width:70%;
  box-sizing: border-box;
  text-align: center;

}
.product-price{
  margin-top:10px;
  box-sizing: border-box;
  text-align: center;  
}
.product-description{
  position:fixed;
  height:100vh;
  width:100%;
  z-index:5;
  top:0px;
  cursor: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' width='20' height='20' viewBox='0 0 20 20'><path d='M19 2.414L17.586 1 10 8.586 2.414 1 1 2.414 8.586 10 1 17.586 2.414 19 10 11.414 17.586 19 19 17.586 11.414 10'/></svg>"),pointer;
  background: rgba(15, 15, 17, 0.5);
}
.product-description-modal{
  width:1100px;
  position:absolute;
  top:0;
  bottom:0;
  right:0;
  left:0;
  margin:auto;
  cursor:auto;
  height:700px;
  background-color: white;
  display:flex;
  flex-wrap: wrap;
}
.product-description-modal > div{
  flex-basis: 50%;
  height:100%;
}
.product-description-info{
  display:flex;
  flex-direction: column;
  align-items: center;
  padding:21px;
  text-align: center;
  box-sizing: border-box;
  overflow-y: auto;
}
.product-description-info>div:nth-child(1){
  color: #23232c;
  font-size:2.2rem;
  font-family: "Sofia Pro",sans-serif;
  padding:0px 2px 0px 1px
}
.product-description-info>div:nth-child(2){
margin-top: 20px;
font-size: 1.2rem;
color:grey
}
.product-description-info>div:nth-child(3){
margin-top: 20px;
font-size: 2.3rem;
font-family: "Poppins",sans-serif;
font-weight: normal;
}
.product-description-info>div:nth-child(4){
margin-top: 50px;
font-family: "Sofia Pro",sans-serif;
display:flex;
flex-wrap: wrap;
justify-content: center;
align-content: space-between;
min-height: 59px;
max-height: 200px;
box-sizing: border-box;
}
.product-description-info>div:nth-child(4)>*{
  margin:8px
}
.product-description-info>div:nth-child(4)>div:nth-child(1){
  border:1px solid grey;
  width:120px;
  display:flex;
  justify-content: center;
  align-items:center;
  font-size: large;
  overflow-x: auto;
}
.product-description-info>div:nth-child(4)>div:nth-child(1)>*{
  flex:1;
}
.product-description-info>div:nth-child(4)>div:nth-child(1)>span:not(.product-description-info>div:nth-child(4)>div:nth-child(1)>span:nth-child(2)){
  cursor:pointer
}
.product-description-info>div:nth-child(4)>div:nth-child(2){

  width:210px;
  display:flex;
  align-items: center;
  justify-content: center;
  color:white;
  background-color: black;
cursor: pointer;

}
.product-description-info>div:nth-child(4)>div:nth-child(2)>*{
  margin:8px;
}
.product-description-info>div:nth-child(4)>div:nth-child(3){
   border:1px solid grey;
  width:59px;
  display:flex;
  color:grey;
  justify-content: center;
  align-items: center;
  font-size: larger;
  cursor: pointer;
}
.prod-desc-crossicon{
  position:absolute;
  right:10px;
  top:10px;
  font-size: x-large;
  cursor: pointer;
  color:grey !important;
  font-weight: lighter !important;
}
@media only screen and (max-width: 1100px) {
  .product-description-modal > div{
  flex-basis: 100%;
  height:50%;
}
.product-description-modal{
  width:500px;
}
.product-description-info > div:nth-child(2){
  display: none;
}
.product-description-info>div:nth-child(1){
  font-size: 1.8rem;
  padding:0
}
.product-description-info>div:nth-child(3){
  font-size: 1.8rem;
}
.product-description-info>div:nth-child(4){
  margin-top: 5px;
}

.product-description-info>div:nth-child(4)>div:nth-child(2){
  order: -1;
}
}
@media only screen and (max-width: 500px) {
  
.product-description-modal{
  width:80%;
  height:80%
}
}